export default {
	name: "uni-load-more",
	data() {
		return {
			contentdown: "正在加载1...",
			contentrefresh: "正在加载...",
			contentnomore: "没有更多数据啦1"
		};
	},
	props: {
		status: {
			//上拉的状态：more-loading前；loading-loading中；noMore-没有更多了
			type: String,
			default: 'more'
		},
		showIcon: {
			type: Boolean,
			default: true
		},
		color: {
			type: String,
			default: "#777777"
		},
		contentText: {
			type: Object,
			default () {
				return {
					contentdown: "正在加载...",
					contentrefresh: "正在加载...",
					contentnomore: "没有更多数据啦"
				};
			}
		}
	},
	created() {
		this.contentnomore = this.$t("index").contentnomore;
		this.contentdown = this.$t("index").contentdown;
		this.contentrefresh = this.$t("index").contentrefresh;
	}
}
